import { Component, OnDestroy, OnInit } from '@angular/core';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { fadeInRight400ms, fadeInUp400ms, scaleIn400ms, stagger40ms } from '@vex/animations';
import { Link } from '@vex/interfaces/link.interface';

import { Project, User } from '@data/schema';
import { LayoutConfigService } from '@layout/services/layout-config.service';
import { UserDataService } from '@users/services/user-data.service';

import { UserProfileIcons } from './services/user-profile-icons.service';

@Component({
  selector: 'user-profile',
  templateUrl: './user-profile.component.html',
  styleUrls: ['./user-profile.component.scss'],
  animations: [
    fadeInUp400ms,
    fadeInRight400ms,
    scaleIn400ms,
    stagger40ms,
  ],
})
export class UserProfileComponent implements OnInit, OnDestroy {
  user: User;
  projects: Project[];

  constructor(
    public icons: UserProfileIcons,
    private layoutConfigService: LayoutConfigService,
    private route: ActivatedRoute,
    private router: Router,
    private userDataService: UserDataService
  ) {
    this.layoutConfigService.setIkaros();
  }

  ngOnInit() {
    this.projects =  this.route.snapshot.data.projects;

    this.userDataService.user$.subscribe(user => {
      this.user = user;
    });
  }

  scenariosPath() {
    return '/scenarios';
  }

  projectQueryParams(project) {
    return {
      project_id: project.id,
    };
  }

  ngOnDestroy() {
    this.layoutConfigService.setZeus();
  }
}
