import { SelectionModel } from '@angular/cdk/collections';
import { Location } from '@angular/common';
import { AfterViewInit, Component, Input, OnDestroy, OnInit, ViewChild } from '@angular/core';
import { FormControl } from '@angular/forms';
import { MatDialog } from '@angular/material/dialog';
import { MatFormFieldDefaultOptions, MAT_FORM_FIELD_DEFAULT_OPTIONS } from '@angular/material/form-field';
import { MatPaginator } from '@angular/material/paginator';
import { MatSelectChange } from '@angular/material/select';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { ActivatedRoute, ActivatedRouteSnapshot, Router } from '@angular/router';

import { untilDestroyed } from 'ngx-take-until-destroy';
import { filter } from 'rxjs/operators';

import { fadeInUp400ms, stagger40ms } from '@vex/animations';
import { TableColumn } from '@vex/interfaces/table-column.interface';

import { OrganizationResource } from '@core/http/organization-resource.service';
import { OrganizationsUserResource } from '@core/http/organizations-user-resource.service';
import { FileService } from '@core/utils/file.service';
import { Organization, User } from '@data/schema';
import { OrganizationsCreateComponent } from '@organizations/components/organizations-create/organizations-create.component';
import { UserDataService } from '@users/services/user-data.service';

import { LayoutConfigService } from '@layout/services/layout-config.service';
import { aioTableData, aioTableLabels } from './mock/user-organizations-data';
import { OrganizationsUserRoleData } from './services/organizations-user-role-data.service';
import { UserOrganizationsData } from './services/user-organizations-data.service';
import { UserOrganizationsIcons } from './services/user-organizations-icons.service';

@Component({
  selector: 'user-organizations',
  templateUrl: './user-organizations.component.html',
  styleUrls: ['./user-organizations.component.scss'],
  animations: [
    fadeInUp400ms,
    stagger40ms,
  ],
  providers: [
    {
      provide: MAT_FORM_FIELD_DEFAULT_OPTIONS,
      useValue: {
        appearance: 'standard',
      } as MatFormFieldDefaultOptions,
    },
  ],
})
export class UserOrganizationsComponent implements OnInit, AfterViewInit, OnDestroy {
  user: User;

  layoutCtrl = new FormControl('boxed');

  // Table settings
  @Input()
  columns: TableColumn<Organization>[] = [
    { label: 'Role', property: 'currentUserRole', type: 'text', visible: true},
    { label: 'Name', property: 'name', type: 'text', visible: true},
    { label: 'Description', property: 'description', type: 'text', visible: true},
    { label: 'Leave', property: 'button', type: 'button', visible: true, onclick: ($event, organization: Organization) => {
        $event.stopPropagation();
        this.destroyOrganizationsUser(organization);
      },
    },
  ];
  pageIndex = this.route.snapshot.queryParams.page || 0;
  pageSize = 10;
  pageSizeOptions: number[] = [5, 10, 20, 50];
  dataSource: MatTableDataSource<Organization> | null;
  selection = new SelectionModel<Organization>(true, []);
  searchCtrl = new FormControl();
  labels = aioTableLabels;

  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;

  constructor(
    public icons: UserOrganizationsIcons,
    public organizationsUserRoleData: OrganizationsUserRoleData,
    private activatedRoute: ActivatedRoute,
    private dialog: MatDialog,
    private file: FileService,
    private layoutConfigService: LayoutConfigService,
    private location: Location,
    private organizationResource: OrganizationResource,
    private organizationsUserResource: OrganizationsUserResource,
    private router: Router,
    private route: ActivatedRoute,
    private userOrganizationsData: UserOrganizationsData,
    private userDataService: UserDataService
  ) {
    this.layoutConfigService.setIkaros();
  }

  get visibleColumns() {
    return this.columns.filter(column => column.visible).map(column => column.property);
  }

  ngOnInit() {
    this.dataSource = new MatTableDataSource();
    this.userOrganizationsData.organizations$.subscribe(curOrganizations => {
      this.dataSource.data = curOrganizations;
    });

    const organizations = this.route.snapshot.data.organizations.filter(organization => {
      return !organization.isShadow;
    }).map(
      organization => {
        const o = new Organization(organization);
        o.currentUserRole = this.organizationsUserRoleData.getRoleTitle(o.currentUserRole);
        return o;
      }
    );
    this.userOrganizationsData.set(organizations);

    this.searchCtrl.valueChanges.pipe(
      untilDestroyed(this)
    ).subscribe(value => this.onFilterChange(value));

    this.userDataService.user$.subscribe(user => {
      this.user = user;
    });
  }

  ngAfterViewInit() {
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  getOrganizations(params?: object) {
    this.organizationResource.index(
      params
    ).subscribe(
      (organization: Organization[]) => {
        const o = new Organization(organization);
        o.currentUserRole = this.organizationsUserRoleData.getRoleTitle(o.currentUserRole);
        this.userOrganizationsData.add(o);
      },
      error => {

      }
    );
  }

  createOrganization(data: FormData) {
    this.organizationResource.create(
      data
    ).subscribe(
      (organization: Organization) => {
        this.userOrganizationsData.add(organization);
      },
      error => {

      }
    );
  }

  showOrganization(organization: Organization) {
    const path: string = this.file.join('/', 'organizations', organization.id, 'settings');
    const snapshot: ActivatedRouteSnapshot = this.route.snapshot;

    this.router.navigate([path]);
  }

  updateOrganizationsUser(organization: Organization, params: object) {
    this.organizationsUserResource.update(
      organization.id, this.user.id, params
    ).subscribe(res => {
      // TODO: Update view iwth new role
    });
  }

  destroyOrganizationsUsers(organizations: Organization[]) {
    organizations.forEach(organization => {
      this.destroyOrganizationsUser(organization);
    });
  }

  destroyOrganizationsUser(organization: Organization) {
    this.organizationsUserResource.destroy(
      organization.id, this.user.id
    ).subscribe(res => {
      this.userOrganizationsData.delete(organization.id);
      this.selection.deselect(organization);
    });
  }

  /**
   *
   * Table methods
   *
   */
  openCreateDialog() {
    const dialogRef = this.dialog.open(OrganizationsCreateComponent, {
      width: '600px',
    });

    const onCreateSub = dialogRef.componentInstance.onCreate.subscribe(
      ($event) => {
        this.createOrganization($event);
      }
    );

    dialogRef.afterClosed().subscribe((organization: Organization) => {
      onCreateSub.unsubscribe();
    });
  }

  onFilterChange(value: string) {
    if (!this.dataSource) {
      return;
    }
    value = value.trim();
    value = value.toLowerCase();
    this.dataSource.filter = value;
  }

  toggleColumnVisibility(column, event) {
    event.stopPropagation();
    event.stopImmediatePropagation();
    column.visible = !column.visible;
  }

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    this.isAllSelected() ?
      this.selection.clear() :
      this.dataSource.data.forEach(row => this.selection.select(row));
  }

  trackByProperty<T>(index: number, column: TableColumn<T>) {
    return column.property;
  }

  /** On page change, add queryParam 'page' to URL */
  onPaginateChange($event) {
    const queryParams = Object.assign({}, this.route.snapshot.queryParams);
    queryParams.page = $event.pageIndex;

    const url = this
        .router
        .createUrlTree([], {relativeTo: this.activatedRoute, queryParams})
        .toString();

    this.location.go(url);
  }

  ngOnDestroy() {
    this.layoutConfigService.setZeus();
  }
}
