import { Injectable } from '@angular/core';

import { OrganizationsUserRoles, OrganizationsUserRoleTitles } from '@data/schema/organizations-user';

@Injectable({
  providedIn: 'root',
})
export class OrganizationsUserRoleData {
  roleTitles: Array<string>;
  roleTitlesMap: object;
  titleRolesMap: object;

  constructor() {
    this.roleTitlesMap = {};
    this.titleRolesMap = {};

    let titleKeys = Object.keys(OrganizationsUserRoleTitles);
    titleKeys = titleKeys.slice(titleKeys.length / 2);
    let roleKeys = Object.keys(OrganizationsUserRoles);
    roleKeys = roleKeys.slice(roleKeys.length / 2);
    roleKeys.forEach((value, index) => {
      this.roleTitlesMap[value] = titleKeys[index];
      this.titleRolesMap[titleKeys[index]] = value;
    });

    this.roleTitles = Object.values(this.roleTitlesMap);
  }

  getRoleTitle(role: string) {
    return this.roleTitlesMap[role];
  }
}
