import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';

import { VexRoutes } from '@vex/interfaces/vex-route.interface';

import { OrganizationPaymentMethodsResolver } from '@organizations/services/organization-payment-methods-resolver.service';
import { ProjectsResolver } from '@projects/services/projects-resolver.service';

import { UserBillingComponent } from './pages/user-billing/user-billing.component';
import { UserOrganizationsComponent } from './pages/user-organizations/user-organizations.component';
import { UserProfileComponent } from './pages/user-profile/user-profile.component';
import { UserOrganizationsResolver } from './services/user-organizations-resolver.service';
import { UserPaymentsResolver } from './services/user-payments-resolver.service';
import { UserResolver } from './services/user-resolver.service';
import { UserSubscriptionResolver } from './services/user-subscription-resolver.service';
import { UsersComponent } from './users.component';

const routes: VexRoutes = [
  {
    path: ':user_id',
    component: UsersComponent,
    data: {
      toolbarShadowEnabled: true,
      containerEnabled: true,
    },
    resolve: {
      user: UserResolver,
    },
    children: [
      {
        path: '',
        component: UserProfileComponent,
        resolve: {
          organizations: UserOrganizationsResolver,
          projects: ProjectsResolver,
        },
      },
      {
        path: 'profile',
        component: UserProfileComponent,
        resolve: {
          organizations: UserOrganizationsResolver,
          projects: ProjectsResolver,
        },
      },
      {
        path: 'organizations',
        component: UserOrganizationsComponent,
        resolve: {
          organizations: UserOrganizationsResolver,
        },
      },
      {
        path: 'billing',
        component:  UserBillingComponent,
        resolve: {
          subscription: UserSubscriptionResolver,
          payments: UserPaymentsResolver,
          paymentMethods: OrganizationPaymentMethodsResolver,
        },
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class UsersRoutingModule {
}
