import { Injectable } from '@angular/core';

import { BehaviorSubject } from 'rxjs';

import { Organization } from '@data/schema/organization';

@Injectable({
  providedIn: 'root',
})
export class UserOrganizationsData {
  private subject = new BehaviorSubject(null);
  organizations$ = this.subject.asObservable();
  organizations: Organization[] = [];

  constructor() { }

  set(organizations: Organization[]) {
    this.organizations = organizations;
    this.subject.next(organizations);
  }

  add(organization: Organization) {
    this.organizations.unshift(new Organization(organization));
    this.set(this.organizations);
  }

  delete(id: number) {
    this.organizations.splice(
      this.organizations.findIndex((existingOrganization) => {
        return existingOrganization.id === id;
      }), 1);

    this.set(this.organizations);
  }
}
